import { Component, OnInit, Input } from '@angular/core';
import { LangService } from 'src/app/services/lang.service';
import { ExperienceService } from 'src/app/services/experience.service';

@Component({
  selector: 'app-experience-img',
  templateUrl: './experience-img.component.html',
  styleUrls: ['./experience-img.component.css']
})

export class ExperienceImgComponent implements OnInit {  
  imgInfo = [];
  ln: string;
  clicks:number = 1;
  maxQttImg: number = 19;
  timeout:number = 10000;
  path1:string = "./img/";
  path2:string = "/";
  path3:string = ".png"
  _id:string;
  imgWay:string;

  @Input() id:string;  

  constructor(private ls: LangService, 
    private ex: ExperienceService) { 
    
  }

  ngOnInit() {
    this.imgWay = "./img/"+this.id+"/"+this.clicks+".JPG";

    this.ln = this.ls._lang;

    this.ls.langClick.subscribe( m => this.ln = m);

    this.ex.getImgInfo().subscribe(_imgInfo => {
      this.imgInfo = _imgInfo;
      console.log(this.imgInfo);
    });
  } 
  
  nextImg():number { 
    if (this.clicks<=this.maxQttImg-1) {
     this.clicks++;
    }
    else{ 
      return this.clicks = 1;      
    }
  };

  prevImg():number { 
    if (this.clicks >=2) {
     this.clicks--;
    }
    else{ 
      return this.clicks = this.maxQttImg;      
    }
  };

  labelImg(x) {
    this.clicks = x;
  }
}
